.app {
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
}

.app::before {
  content: "";
  background: url("./assets/cherry.jpg") no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.container {
  width: 50%;
  margin: 0px auto;
  background-color: rgba(0, 0, 0, 0.6);
  /* text-align: center; */
  padding: 20px;
  border-radius: 20px;
  color: white;
}

.search {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-bottom: 20px;
}

.search input {
  width: 100%;
  background-color: #222;
  border: none;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  padding: 10px 20px;
  color: white;
  outline: none;
}

input[type="text"]
{
    font-size:18px;
}

.search button {
  background-color: #222;
  color: white;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  padding: 0px 20px;
  font-size:18px;
}

.weather-info h1 {
  font-size: 3rem;
}

.weather-status {
  display: flex;
  /* justify-content: center; */
  align-items: center;
}

.weather-status img {
  width: 100px;
  height: 100px;
}

.weather-detail {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.weather-detail span {
  width: 50%;
  color: #aaa;
  padding: 5px 0px;
}


@media (max-width: 425px) {
  .container {
    width: 90%;
  }
}